.container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  animation-name: opacityUp;
  animation-duration: 1s; }

@keyframes opacityUp {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.redes {
  margin-top: 20px;
  z-index: 10;
  animation-name: opacityUp;
  animation-duration: 3s; }
  .redes a {
    margin-right: 20px;
    opacity: 0.6; }
    .redes a:hover {
      opacity: 1;
      animation-name: animate-hover;
      animation-duration: 0.6s; }

@keyframes animate-hover {
  0% {
    opacity: 0.6; }
  100% {
    opacity: 1; } }
    .redes a:last-child {
      margin-right: 0; }
